.details_bottom {
  display: flex;
  flex-wrap: wrap;
  text-align: start;
  padding: 2rem;
  padding-top: 6rem;
  padding-bottom: 1rem;
  /* border-top: 5rem rgb(240, 240, 240) solid; */
  background-color: rgb(240, 240, 240);
}
.details_bottom > div {
  padding-left: 2rem;
  padding-right: 2rem;
}
.img_logo {
  width: 7rem;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}
.icon {
  display: flex;
}
.icon > div {
  margin: 1rem;
}
.copyright {
  display: flex !important;
  flex-wrap: wrap;
  background-color: rgb(240, 240, 240);
  justify-content: space-between;
  padding-left: 4rem;
  padding-right: 8rem;
  padding-bottom: 1rem;
  padding-top: 1rem;
  border-top: 2px rgb(173, 173, 173) solid;
}